.headerSection {
  padding-top: 40px;
  padding-bottom: 20px;
}

.feedData {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.feedContent {
  display: flex;
  flex-direction: row;
  justify-content: left;
  width: 600px;
  min-height: 600px;
  max-height: 75vh;
  margin-right: 68px;
}

.feedChart {
  display: flex;
  flex-direction: column;
  width: 580px;
}

.currentOrders {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.currentChart {
  display: flex;
  flex-direction: column;
  width: 272px;
}

.ul {
  list-style-type: none;
  max-height: 220px;
  width: 240px;
  /* overflow-y: scroll; */
  padding: 0;
  padding-left: 10px;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

